export default {
	data() {
		return {
			list: [],
			lists: [],
			zhuanyiss: [],
			alipy: true,
			wxpay: false,
			flag: true,
			baseUrl: '',
			vip_id: '',
			yizhi: '已知HSCODE',
			miaoshu: '商品描述',
			zhuanyi: '转译查询',
			isCheck: -1,
			isCheckTwo: -1,
			isCheckthree:-1,
			payid:"",
			prices:"0",
			userName:"",
			days:"",
			userImg:"",
			dui:false
		};
	},
	onShow() {
		this.listVip(1);
		this.listVips(2);
		this.zhuanyis(3);
		this.userinfo();
		uni.setNavigationBarTitle({
				title: this.$t('index.renzheng')
		})	
	},
	computed: {
		i18n() {
			return this.$t('index');
		}
	},
	methods: {
		payTypeId(id, index,price) {
			console.log(id);
			this.isCheckTwo = -1;
			this.isCheckthree = -1;
			this.isCheck = index;
			this.payid = "";
			this.payid = id;
			// this.prices = "";
			this.prices = price;
			
		},
		payTypeIdtwo(id, index,price) {
			this.isCheck = -1;
			this.isCheckthree = -1;
			this.isCheckTwo = index;
			console.log(id);
			this.payid = "";
			this.payid = id;
			this.prices = price;
		},
		payTypeIdthree(id, index,price) {
			this.isCheck = -1;
			this.isCheckTwo = -1;
			this.isCheckthree = index;
			console.log(id);
			this.payid = "";
			this.payid = id;
			this.prices = price;
		},
		userinfo() {
			var that = this;
			that.baseUrl = that.websiteUrl;
			let token = uni.getStorageSync('storage_token');
		
			uni.request({
				url: that.baseUrl + '/api/userInfo',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: function(res) {
					if (res.data.code == 200) {
						console.log(res.data);
						// console.log(res.data.data.account);
						// console.log(res.data.data.vip_balance_days);
						// console.log(res.data.data.portrait);
						that.userName = res.data.data.account;
						that.days = res.data.data.vip_balance_days || 0;
						that.userImg = res.data.data.portrait;
					}
				}
			});
		},
		listVip(type) {
			this.baseUrl = this.websiteUrl;
			console.log(this.baseUrl);
			let token = uni.getStorageSync('storage_token');
			console.log(token);
			uni.request({
				url: this.baseUrl + '/api/getVipList',
				method: 'GET',
				data: {
					type: type
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					if (res.data.code == 200) {
						this.list = res.data.data;

						console.log(this.list);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		listVips(type) {
			this.baseUrl = this.websiteUrl;
			console.log(this.baseUrl);
			let token = uni.getStorageSync('storage_token');
			console.log(token);
			uni.request({
				url: this.baseUrl + '/api/getVipList',
				method: 'GET',
				data: {
					type: type
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					if (res.data.code == 200) {
						this.lists = res.data.data;

						console.log(this.lists);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		zhuanyis(type) {
			this.baseUrl = this.websiteUrl;
			console.log(this.baseUrl);
			let token = uni.getStorageSync('storage_token');
			console.log(token);
			uni.request({
				url: this.baseUrl + '/api/getVipList',
				method: 'GET',
				data: {
					type: type
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					if (res.data.code == 200) {
						this.zhuanyiss = res.data.data;

						console.log(this.zhuanyiss);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		gerenClick() {
			console.log(this.wxpay);
			this.wxpay = true;
			this.alipy = false;
		},
		qiyeClick() {
			console.log(this.alipy);
			this.wxpay = false;
			this.alipy = true;
		},
		sure() {
			// this.flag = false;
			var alipy;
			if (this.alipy == true) {
				alipy = '2';
			} else {
				alipy = '1';
			}
			console.log(alipy);
			console.log(this.payid);
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');
			// pay_type  1/微信 2/支付宝  3/小程序
			// type 1/VIP 2/超出后购买 3/自动续费 4/会员免费单词查询
			uni.request({
				url: this.baseUrl + '/api/vipOrder',
				method: 'POST',
				data: {
					pay_type: alipy,
					type: '1',
					vip_id: this.payid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					if (res.data.code == 200) {
						console.log(res.data);
						// console.log(res.data.data.payData);
						// orderInfo 微信/支付宝订单数据
						// provider:provider  wxpay/微信 alipay支付宝

						let orderInfo = res.data.data.payData;
						var paysss;
						console.log(orderInfo);
						if(alipy == 1){
							paysss = "wxpay";
							uni.requestPayment({
								provider: paysss,
								orderInfo: JSON.stringify(orderInfo),
								success: function(res) {
									console.log('success:' + JSON.stringify(res));
								},
								fail: function(err) {
									console.log('fail:' + JSON.stringify(err));
								}
							});
						}else if(alipy == 2){
							let orderInfo = res.data.data.payData.payData;
							paysss = "alipay";
							uni.requestPayment({
								provider: paysss,
								orderInfo: orderInfo,
								success: function(res) {
									console.log('success:' + JSON.stringify(res));
								},
								fail: function(err) {
									console.log('fail:' + JSON.stringify(err));
								}
							});
						}
						console.log(paysss);
						// uni.requestPayment({
						// 	provider: paysss,
						// 	orderInfo: JSON.stringify(orderInfo),
						// 	success: function(res) {
						// 		console.log('success:' + JSON.stringify(res));
						// 	},
						// 	fail: function(err) {
						// 		console.log('fail:' + JSON.stringify(err));
						// 	}
						// });
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 支付
		xuFei(e, type) {
			this.flag = true;
			this.vip_id = e;
			console.log('当前id是' + this.vip_id);
			console.log('当前类型是' + type);
		}
	}
};
